.service{
    position: relative;   
    padding: 24px 0 50px;
    @media only screen and (min-width:992px){
        padding: 50px 0 142px;
    } 
    .picture__image{
        object-fit: cover; 
        object-position: center top;       
        height: 100%;
        max-width: 100%;
        width: 100%;
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
    }
    // .picture__image--left{
    //     object-position: left top;
    // }
    // .picture__image--right{
    //     object-position: right top;
    // }
    // .picture__image--center{
    //     object-position: center top;
    // }
    .container{
        max-width: 1193px;
        width: 100%;
        position: relative;
        z-index: 1;
        @media only screen and (min-width:992px){
            width: 95%;
        }
        @media (max-width:600px){
            padding: 0 20px;
        }
    }
    .service__header{
        margin-bottom: 33px;
        @media only screen and (min-width:992px){
            margin-bottom: 100px;
        }
    }
    .service__title{
        @include root-tipography-regular;
        font-size: 32px;
        line-height: 40px;
        text-align: center;
        margin: 0 0 20px;
        @media only screen and (min-width:992px){
            font-size: 40px;
            line-height: 48px;
        }
    }
    .service__description{
        @include root-tipography-regular;
        font-size: 16px;
        line-height: 24px;
        text-align: center;
        max-width: 620px;
        width: 100%;
        margin: 0 auto;
        @media only screen and (min-width:992px){
            font-size: 20px;
            line-height: 25px;
        }
    }

    .service__footer{
        display: none;
        text-align: center;
        .container{
            width: 85%;
        }
    }
    .service__btn{
        width: 100%;
        max-width: 200px;
        margin: 0 auto 0;
        border-radius: 39px;
        height: 48px;
        @include root-tipography;
        font-size: 16px;
        line-height: 42px;
        text-align: center;
        &::after{
            content: 'más';
            display: inline-block;
        }
    }
    .service__btn.displayOn{
        &::after{
            content: 'menos';
            display: inline-block;
        }
    }
    .service__list{
        display: block;
        @media only screen and (min-width:1200px){
            margin: 0 -20px;
        }
        @media (max-width:1200px) and (min-width:992px){
            margin: 0 -10px;
        }
        @media only screen and (min-width:992px){
            display: flex;
            justify-content: center;
        }
    }
    .service__slick{
        .slick-next{
            right: 0;
        }
        .slick-prev{
            left: 0;
        }
        .slick-prev, .slick-next{
            visibility: hidden;
            opacity: 0;
            width: 40px;
            height: 40px;
            @media only screen and (min-width:992px){
                visibility: visible;
                opacity: 1;
            }
        }
        .slick-prev:before, .slick-next:before{
            background: transparent;
            font-size: 40px;
            padding: 0;
        }
        .slick-dots{
            bottom: -10px;
            @media only screen and (min-width:992px){
                bottom: -80px;
            }
        }
        .slick-dots li button:before{
            display: none;
        }
        .slick-dots li{
            width: 10px;
            height: 10px;            
        }
        .slick-dots li.slick-active button{
            background: $color_texto;
        }
        .slick-dots li button{
            padding: 0;
            width: 100%;
            height: 100%;
            border-radius: 100%;
            background: #c4c4c4;
        }
    }
    .service__unslick{
        .service__item:nth-child(n+5){
            display: none;
        }
    }
    .service__item{
        text-align: center;
        
        @media only screen and (min-width:1201px){
            padding: 0 20px;
        }
        @media (max-width:1200px) and (min-width:992px){
            padding: 0 10px;
        }
        @media(max-width:991px){
            padding: 10px 10px;
        }
        
        .container{
            max-width: 340px;
            width: 100%;
            height: 100%;
            position: relative;
            padding: 15px 0;
            @media only screen and (min-width:992px){
                padding: 0 0;
            }
        }
        .image,
        .icon{
            min-width: 64px;
            display: inline-block;
            margin: auto;
        }
        .icon{
            span[class^='icon']{
                font-size: 64px;
                display: inline-block;
            }
        }
        .image{
            img{
                display: block;
                margin: auto;
            }
        }
        .title{
            @include root-tipography;
            font-size: 16px;
            line-height: 24px;
            text-align: center;
            margin: 13px 0;
        }
        .description{
            @include root-tipography;
            font-size: 16px;
            line-height: 24px;
            text-align: center;
            margin: 0;
            @media only screen and (min-width:992px){
                font-size: 18px;
            }
            a{                
                text-decoration: underline;
            }
        }
        .price{
            @include root-tipography;
            font-size: 28px;
            line-height: 24px;
            text-align: center;
            margin: 10px 0 0;
            @media only screen and (min-width:992px){
                font-size: 30px;
            }
        }
        .information{
            @include root-tipography;
            font-size: 16px;
            line-height: 24px;
            margin-top: 25px;
            display: inline-block;
            vertical-align: middle;
            @media only screen and (min-width:992px){
                font-size: 18px;
            }
            span[class^='icon']{
                width: 16px;
                display: inline-block;
                position: relative;
                top: 3px;
                @media only screen and (min-width:992px){
                    font-size: 18px;
                }
            }
            &:focus{
                outline: inherit;
            }
        }
        .item__blur{
            background: rgba(255,255,255,0.10);
            backdrop-filter: blur(15px);
            border-radius: 36px;            
            padding: 32px 20px !important;
            @media only screen and (min-width:991px){
                padding: 28px 20px !important;
            }
        }
        &:focus{
            outline: inherit;
        }
    }
}
.service__font--white{
    color: #ffffff;
    .information{
        color: #ffffff;
    }
    .description{
        a{
            color: #ffffff;
        }
    }
}
.service__font--gray{
    color: #50535A;
    .slick-dots li.slick-active button{
        background: #50535A !important;
    }
    .slick-prev:before, .slick-next:before{
        color: #50535A !important;
    }
}