// Project dependencies
@import 'project-dependencies/swiper-slider';
@import 'project-dependencies/variables';
@import 'project-dependencies/fonts';


.aui{
    // Patterns
    @import 'patterns/global';
    // Componentes
    @import 'components/tipography';
    @import 'components/modal';
    @import 'components/button';
    //solo estilos de prueba checkbox
    .field--checkbox{
        position: absolute;
        bottom: 20px;
        right: 20px;
        color: #fff;
        z-index: 999999;
        input{
            width: 20px;
            height: 20px;
        }
        label{
            display: inline-block;
            vertical-align: super;
        }
    }

    // Sections
    @import 'sections/carrusel-planes';
    @import 'sections/contactanos';
    @import 'sections/servicios';
    @import 'sections/equipment';
    @import 'sections/video';
}