@mixin btn-tipography {
    font-family: Telefonica;
    font-style: normal;
    font-weight: normal;   
}
.movistar--green{
    .btn{
        &.btn--primary{
            background-color: $color_verde;
        }
    }
}
.movistar--blue{
    .btn{
        &.btn--primary{
            background-color: $color_cian;
        }
    }
}
.btn.btn--primary{
    @include btn-tipography;
    font-size: 16px;
    line-height: 42px;
    height: 42px;
    padding: 0 34px;
    width: auto;
    display: inline-block;
    box-shadow: inherit;
    border-radius: 4px;
    @media only screen and (min-width: 600px){
        font-size: 18px;
        padding: 0 35px;
        line-height: 48px;
        height: 48px;
    }
}

//Botones nueva linea Grafica
.btn__cian{
    background: $color_cian;
    color: #ffffff;
    border: 2px solid $color_cian;
}
.btn__azul{
    background: $color_azul;
    color: #ffffff;
    border: 2px solid $color_azul;
}
.btn__flat{
    background: transparent;
    color: #ffffff;
    border: 2px solid #ffffff;
}
.btn__flat--gris{
    background: transparent;
    color: $color_texto;
    border: 2px solid $color_texto;
}
.btn__flat--cian{
    background: transparent;
    color: $color_cian;
    border: 2px solid $color_cian;
}
.btn__flat--azul{
    background: transparent;
    color: $color_azul;
    border: 2px solid $color_azul;
}