.contact{
    padding: 72px 0;
    @media only screen and (min-width: 600px){
        padding: 50px 0;
    }
    .container{
        width: 80%;
        max-width: 683px;
    }
    .contact__header{
        margin-bottom: 11px;
        @media only screen and (min-width: 600px){
            margin-bottom: 32px;
        }
    }
    .contact__title{
        @include root-tipography-bold;
        font-size: 36px;
        line-height: 45px;
        text-align: center;
        color: #50535A;
        margin: 0 0 10px;
        @media only screen and (min-width: 600px){
            font-size: 40px;
            line-height: 50px;
        }
    }
    .contact__description{
        @include root-tipography;
        font-size: 16px;
        line-height: 24px;
        text-align: center;
        color: #50535A;
        margin: 0;
        @media only screen and (min-width: 600px){
            font-size: 24px;
            line-height: 24px;
        }
    }
    .contact__list{
        display: flex;
        justify-content: center;
        margin: 0 -16px;
        @media only screen and (min-width: 600px){
            margin: 0 -22px;
        }
    }
    .contact__item{
        padding: 0 16px;
        @media only screen and (min-width: 600px){
            padding: 0 22px;
        }
    }
    .contact__btn{
        width: 64px;
        display: block;
        cursor: pointer;
        border: none;
        background: transparent;
        padding: 0;
        @media only screen and (min-width: 600px){
            width: 90px;
        }
        &:focus{
            background: transparent;
        }
        .icon--image{
            width: 64px;
            height: 64px;
            @media only screen and (min-width: 600px){
                width: 90px;
                height: 90px;
            }
        }
        .description{
            @include root-tipography;
            font-size: 16px;
            line-height: 20px;
            text-align: center;
            color: $color_cian;
            margin: 5px 0 0;
            @media only screen and (min-width: 600px){
                font-size: 18px;
                line-height: 22px;
            }
        }
    }    
}