.video__gallery{
    padding: 52px 0 43px;
    @media only screen and (min-width: 992px){
        padding: 46px 0 76px;
    }
    >.container{
        width: 95%;
        opacity: 0;
    }
    .gallery__header{
        max-width: 600px;
        width: 100%;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 62px;
        padding: 0 10px;
        @media only screen and (min-width: 992px){
            margin-bottom: 70px;
            max-width: inherit;
        }
    }
    .gallery__titulo{
        @include root-tipography-regular;
        font-size: 32px;
        line-height: 40px;
        text-align: center;
        margin: 0 0 24px;
        @media only screen and (min-width: 992px){
            font-size: 40px;
            line-height: 48px;
            margin: 0 0 20px;           
        }
    }
    .gallery__description{
        @include root-tipography-regular;
        font-size: 16px;
        line-height: 24px;
        text-align: center;
        @media only screen and (min-width: 992px){
            font-size: 20px;
            line-height: 25px;            
        }
    }
    .gallery__content{
        padding: 0;
        position: relative;
        min-height: 1px;
        margin: 0 -2.5%;
        @media only screen and (min-width: 992px){
            padding: 30px 0;
            margin: 0;
        }
        .container{            
            display: flex;
            justify-content: center;
            width: 100%;
            max-height: 353px;
            @media only screen and (min-width: 992px){
                overflow: hidden;
            }
            @media only screen and (min-width: 1025px){
                max-height: 469px;
            }
        }        
    }
    .gallery__footer{
        margin-top: 70px;
        @media only screen and (min-width: 992px){
            margin-top: 56px;
        }
        .btn{
            width: 100%;
            max-width: 200px;
            height: 48px;
            margin: auto;
            display: block;
            border-radius: 39px;
            box-shadow: inherit;
            @include root-tipography;
            font-size: 16px;
            line-height: 44px;
        }
    }
    .display__list {
        width: 100%;
        margin-bottom: 0;
        @media only screen and (min-width: 992px){
            padding-right: 20px;
            width: 66.6666666667%;            
        }
        .item__caption{
            position: relative;
            min-height: 1px;
            cursor: pointer;
        }
        .item__icon{
            position: absolute;
            width: 20px;
            height: 20px;
            left: 50%;
            top: 50%;
            z-index: 1;
            transform: translate(-50%, -50%);
            .btn__display{
                font-size: 20px;
                color: #FFFFFF;
                display: block;
                &:focus{
                    outline: inherit
                }
                @media only screen and (min-width: 992px){
                    font-size: 50px;
                }
            }
            .icon{
                display: block;
            }
            @media only screen and (min-width: 992px){
                width: 50px;
                height: 50px;
            }
        }
        .item__poster{
            position: absolute;
            z-index: 1;
            pointer-events: none;            
            width: 100%;
            height: 100%;
            img{
                display: block;
                width: 100%;
                height: 100%;                
                border-radius: 4px;
            }
        }
        .item__video{
            position: relative;
            z-index: 0;
            width: 100%;
            height: 100%;
            display: block;
            max-height: 470px;
            border-radius: 4px;
        }
        .display__item{
            padding: 14px 25px;
            width: 260px;
            transition: all 300ms ease;
            opacity: 0.6;
            @media only screen and (min-width: 992px){
                padding: 0;
                width: auto;
                transition: inherit;
                opacity: inherit;
            }
        }
        .display__item.slick-center{        
            transform: scale(1.24);
            opacity: 1;
        }
        .slick-dots{
            line-height: 0;
            bottom: -30px;
            @media only screen and (min-width: 992px){
                display: none !important;
            }
            li{
                width: 10px;
                height: 10px;                
            }
            button{
                padding: 0;
                background: #C4C4C4;
                width: 10px;
                height: 10px;
                border-radius: 100px;
                &:before{
                    display: none;
                }
            }
            .slick-active{
                button{
                    background: #50535A;
                }
            }
        }
    }
    .thumbs__list {
        width: 33.3333333333%;
        padding-left: 20px;
        margin: 0;     
        visibility: hidden;
        opacity: 0;
        position: absolute;
        @media only screen and (min-width: 992px){
            visibility: visible;
            opacity: 1;
            position: initial;
        }
        .slick-list{
            top: 50%;
            transform: translate(0px, -50%);
        }
        .slick-prev,
        .slick-next{
            width: 30px;
            height: 30px;
        }
        .slick-prev:before{
            content: "\e316";
        }
        .slick-next:before{
            content: "\e313";
        }
        .slick-prev:before,
        .slick-next:before{
            background: transparent;
            padding: 0;
            font-size: 30px;
        }
        .slick-prev{
            left: inherit;
            right: 14.3%;
            top: -10px;
            transform: translate(-14.3%, 0);
        }
        .slick-next{
            top: inherit;
            right: 14.3%;
            bottom: -10px;
            transform: translate(-14.3%, 0);
        }
    }
    .thumbs__item{        
        transition: all 300ms ease;
        padding: 0;
        border: 0;
        opacity: 0.6;
        position: relative;
        .item__caption{
            padding: 7.5% 8%;            
            img{
                display: block;
                width: 100%;
                border-radius: 4px;
            }
        }
        .item__icon{
            display: none;
            position: absolute;
            width: 20px;
            height: 20px;
            left: 50%;
            top: 50%;
            z-index: 1;
            transform: translate(-50%, -50%);
            .icon{
                font-size: 20px;
                color: #FFFFFF;
                display: block;
                &:focus{
                    outline: inherit
                }
                @media only screen and (min-width: 992px){
                    font-size: 40px;
                }
            }
            @media only screen and (min-width: 992px){
                width: 40px;
                height: 40px;
            }
        }
    }
    .thumbs__item.slick-center{        
        transform: scale(1.19);
        opacity: 1;
        .item__icon{
            display: block;
        }
    }
    .poster__information{
        position: absolute;
        bottom: 10px;
        width: 100%;
        @media only screen and (min-width: 992px){
            bottom: 16px;
        }
        .container{
            justify-content: space-between;
            padding: 0 10px 0;
            @media only screen and (min-width: 992px){
                padding: 0 34px 0 20px;
            }
        }
        .title__video{
            @include root-tipography-bold;
            font-size: 14px;
            line-height: 15px;
            color: #FFFFFF;
            @media only screen and (min-width: 992px){
                font-size: 18px;
                line-height: 24px;
            }
        }
        .time__video{
            @include root-tipography-regular;
            font-size: 14px;
            line-height: 15px;
            color: #FFFFFF;
            @media only screen and (min-width: 992px){
                font-size: 18px;
                line-height: 24px;
            }
        }
    }
}