video::-webkit-media-controls-panel {
    width: calc(100% + 30px); /* Adjust as needed */
}
svg{
    display: inline-block;
    font-size: inherit;
    width: 1em;
    height: 1em;
    overflow: visible;
    stroke-width: 0;
    stroke: currentColor;
    fill: currentColor;
}
.align-items--start{
    align-items: flex-start !important; /* IE11 */
}
.align-items--center{
    align-items: center !important; /* IE11 */
}
.align-items--end{
    align-items: flex-end !important; /* IE11 */
}
.movistar--green .banner-hero .slick-dots li.slick-active button{
    background: $color_verde;
}
.movistar--blue .banner-hero .slick-dots li.slick-active button{
    background: $color_cian;
}
.movistar--green .banner-hero .badge{
    background: $color_verde;
    color: #fff;
}
.movistar--blue .banner-hero .badge{
    background: $color_cian;
    color: #fff;
}

.movistar--blue{
    .plans .plan__header .price,
    .plans .plan__header .save,
    .plans .plan__header .amount--network,
    .plans .plan__header .amount--time,
    .plans .plan__body .title,
    .plans .plan__body span[class^=icon],
    .plans .plan__body .exclusive__title,
    .plans .plan__footer .btn-flat{
        color: $color_cian;
    }
    .plans .plan__header .btn{
        background-color: $color_cian;
    }

    .plans .plan__item--recommend .plan__header .price,
    .plans .plan__item--recommend .plan__header .save,
    .plans .plan__item--recommend .plan__header .amount--network,
    .plans .plan__item--recommend .plan__header .amount--time,
    .plans .plan__item--recommend .plan__body .title,
    .plans .plan__item--recommend .plan__body span[class^=icon],
    .plans .plan__item--recommend .plan__body .exclusive__title,
    .plans .plan__item--recommend .plan__footer .btn-flat{
        color: $color_verde;
    }
    .plans .plan__item--recommend .plan__header .btn{
        background-color: $color_verde;
    }
}

.movistar--green{
    .plans .plan__header .price,
    .plans .plan__header .save,
    .plans .plan__header .amount--network,
    .plans .plan__header .amount--time,
    .plans .plan__body .title,
    .plans .plan__body span[class^=icon],
    .plans .plan__body .exclusive__title,
    .plans .plan__footer .btn-flat{
        color: $color_verde;
    }
    .plans .plans__list .plan__header .btn{
        background-color: $color_verde;
    }
    .plans .plan__item--recommend .plan__header .price,
    .plans .plan__item--recommend .plan__header .save,
    .plans .plan__item--recommend .plan__header .amount--network,
    .plans .plan__item--recommend .plan__header .amount--time,
    .plans .plan__item--recommend .plan__body .title,
    .plans .plan__item--recommend .plan__body span[class^=icon],
    .plans .plan__item--recommend .plan__body .exclusive__title,
    .plans .plan__item--recommend .plan__footer .btn-flat{
        color: $color_cian;
    }
    .plans .plan__item--recommend .plan__header .btn{
        background-color: $color_cian;
    }
}


.movistar--green .modal .icon{
    color: $color_verde;
}

.movistar--blue .modal .icon{
    color: $color_cian;
}

.color--cian{
    color: $color_cian !important;
    &:before{
        background: $color_cian;
    }
}
.color--rosado{
    color: $color_rosado !important;
    &:before{
        background: $color_rosado;
    }
}
.color--amarillo{
    color: $color_amarillo !important;
    &:before{
        background: $color_amarillo;
    }
}
.color--morado{
    color: $color_morado !important;
    &:before{
        background: $color_morado;
    }
}
.color--verde{
    color: $color_verde !important;
    &:before{
        background: $color_verde;
    }
}

