.equipment{
    padding: 40px 0;
    @media only screen and (min-width: 600px){
        padding: 50px 0;
    }
    .equipment--carousel{
        max-width: 838px;
        width: 100%;
        padding: 0 10px;
        .equipment__list{
            display: flex;
            justify-content: space-between;
        }
        .equipment__item{
            width: 237px;
            .container{
                width: 100%;                
                opacity: 0.4;
                transition: opacity .25s ease-out; 
            }
        }
        .item__device{
            .image--device{
                display: block;
                width: 100%;                   
            }
        }
        .item__content{
            visibility: hidden;
            opacity: 0;
            transition: all .25s ease-out;
            margin-top: 7px;
            @media only screen and (min-width: 600px){
                margin-top: 15px;
            }
            .recommend{
                @include root-tipography-bold;
                font-size: 14px;
                line-height: 17px;
                text-transform: uppercase;
                color: $color_cian;
                text-align: center;
                margin: 0 0 8px;                
            }
            .description{
                margin: 0;
                @include root-tipography-regular;
                font-size: 18px;
                line-height: 22px;
                text-align: center;
                color: $color_texto;
                @media only screen and (min-width: 600px){
                    font-size: 20px;
                    line-height: 25px;
                }
            }
            .price{
                margin: 0;
                @include root-tipography-regular;                
                font-size: 23px;
                line-height: 28px;
                text-align: center;
                color: $color_texto;
                @media only screen and (min-width: 600px){
                    font-size: 25px;
                    line-height: 35px;
                }
            }
            .link{
                margin-top: 9px;
                text-align: center;
                display: block;
                @include root-tipography;
                font-size: 16px;
                line-height: 24px;
                text-align: center;
                color: $color_cian;
                span[class^='icon']{
                    width: 6px;
                    display: inline-block;
                    position: relative;
                    top: 3px;
                }
                @media only screen and (min-width: 600px){
                    margin-top: 12px;
                }
            }
            .btn{
                max-width: 176px;
                width: 100%;
                height: 48px;
                margin: 16px auto 0;
                display: block;
                border-radius: 70px;
                @include root-tipography;
                font-size: 18px;
                line-height: 24px;
                text-align: center;
                box-shadow: inherit;
                padding: 0;
            }
        }
        .item__float{
            position: absolute;
            width: 100%;
            max-width: 141px;
            top: 40px;
            left: -30px;
            opacity: 0;
            visibility: hidden;
            .float__list{
                display: flex;
                justify-content: space-between;
            }
            .float__item{
                width: 100%;
                max-width: 68px;
                height: 68px;
                background: #ffffff;
                border-radius: 10px;
                display: flex;
                align-items: center;
                justify-content: center;
                .container{
                    width: 100%;    
                    text-align: center;                
                }
                .description--upper{
                    @include root-tipography-regular;
                    font-size: 28px;
                    line-height: 28px;
                    text-align: center;
                    color: $color_cian;
                    margin: 0;
                }
                .description{
                    font-family: "Roboto", sans-serif;
                    font-style: normal;
                    font-weight: 100;
                    font-size: 10px;
                    line-height: 12px;
                    text-align: center;
                    margin: 0;
                    color: $color_cian;
                }
                &:first-of-type{
                    background: $color_cian;
                    .description,
                    .description--upper{
                        color: #ffffff;
                    }
                }
            }
        }
        .swiper-button-next, .swiper-button-prev{
            display: none;
            width: 36px;
            top: 40%;
            transform: translate(0, -60%);
            @media only screen and (min-width: 600px){
                display: block;
            }
            &:focus{
                outline: inherit;
            }
        }
        .swiper-wrapper{
            .swiper-slide-active{
                .container{
                    opacity: 1;
                    transition: opacity .25s ease-in;
                }
                .item__content{
                    opacity: 1;
                    visibility: visible;
                    transition: all .25s ease-in;
                }
                .item__float{
                    opacity: 1;
                    visibility: visible;
                    transition: all .25s ease-in;
                }
            }
        }
        
    }
    .equipment__header{
        max-width: 267px;
        width: 100%;
        margin: 0 auto 36px;
        @media only screen and (min-width: 600px){
            margin-bottom: 65px;
            max-width: inherit;
        }   
    }
    .equipment__title{
        @include root-tipography-regular;
        font-size: 32px;
        line-height: 40px;
        text-align: center;
        color: $color_texto;
        margin: 0;
        @media only screen and (min-width: 600px){
            font-size: 40px;
            line-height: 50px;
        }
    }
    .equipment--block{
        width: 100%;
        padding: 0 19px;
        overflow: hidden;
        .equipment__header{
            margin-bottom: 40px;
            @media only screen and (min-width: 600px){
                margin-bottom: 56px;
            }
        }
        .equipment__list{
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            margin: 0;
            @media only screen and (min-width: 1301px){
                margin: 0 -68px;
            }
            @media (max-width: 1300px) and (min-width: 992px){
                margin: 0 -25px;
            }
        }
        .equipment__item{
            margin-bottom: 80px;
            position: relative;
            max-width: 307px;
            width: 100%;
            padding: 0;
            @media only screen and (min-width: 992px){
                &:nth-last-child(-n+3){
                    margin-bottom: 0;
                }
            }
            @media(max-width: 991px) and (min-width: 769px){
                &:nth-last-child(-n+2){
                    margin-bottom: 0;
                }
            }
            @media(max-width: 768px){
                &:nth-last-child(-n+1){
                    margin-bottom: 0;
                }
            }
            .container{
                width: 100%;
                display: flex;
                justify-content: center;
            }
            @media only screen and (min-width: 1301px){
                padding: 0 68px;
                max-width: 443px;
            }
            @media (max-width: 1300px) and (min-width: 600px){
                padding: 0 25px;
                max-width: 357px;
            }
        }        
        .item__device{
            width: 100%;
            max-width: 130px;
            margin-right: 22px;
            margin-left: 0;
            @media (max-width: 359px) {
                margin-right: 10px;
            }
        }
        .item__content{
            width: 100%;
            max-width: 156px;
            margin: 0;
        }
        .item__float{
            position: absolute;
            top: 40px;                       
            left: -15px;
            z-index: 1;
            @media only screen and (min-width: 1301px){
                left: 50px;
            }
            @media (max-width: 1300px) and (min-width: 600px){
                left: 5px;
            }
            .float__list{
                display: flex;
                justify-content: space-between;
            }
            .float__item{
                width: 100%;
                max-width: 58px;
                height: 58px;
                background: $color_cian;
                border-radius: 10px;
                display: flex;
                align-items: center;
                justify-content: center;                
                .container{
                    width: 100%;    
                    text-align: center;                
                }
                .description{
                    font-family: "Roboto", sans-serif;
                    font-style: normal;
                    font-weight: 100;
                    font-size: 8px;
                    line-height: 10px;
                    text-align: center;
                    margin: 0;
                    color: #ffffff;
                }
            }
        }
        .image__device{
            display: block;
            width: 100%;
        }
        .device__header{
            margin-bottom: 20px;
            .recommend{
                @include root-tipography-bold;
                font-size: 10px;
                line-height: 12px;
                text-transform: uppercase;
                color: $color_cian;
                margin: 0;
            }
            .title{
                @include root-tipography-regular;
                font-size: 24px;
                line-height: 30px;
                color: $color_texto;
                margin: 0;
            }
        }
        .device__content{
            margin-bottom: 20px;
            .description{
                @include root-tipography;
                font-size: 14px;
                line-height: 17px;
                color: $color_texto;
                margin: 0;
                @media (max-width: 359px){
                    font-size: 13px;
                }
            }
            .price{
                @include root-tipography-regular;
                font-size: 24px;
                line-height: 30px;
                color: $color_texto;
                margin: 0 0 10px;
            }
        }
        .device__footer{
            .description{
                @include root-tipography;
                font-size: 14px;
                line-height: 17px;
                color: $color_texto;
                margin: 0;
                @media (max-width: 359px){
                    font-size: 13px;
                }
            }          
            .btn{
                max-width: 156px;
                width: 100%;
                height: 48px;
                border-radius: 70px;
                font-size: 18px;
                line-height: 24px;
                padding: 0;
                margin: 10px 0 0;
                box-shadow: inherit;
            }
        }
        .swiper-pagination{
            display: block;
            bottom: 0;
            @media only screen and (min-width: 992px){
                display: none;
            }
        }
        .swiper-container{
            padding-bottom: 28px;
        }
        .swiper-button-next, .swiper-button-prev{
            display: none;
            top: inherit;
            bottom: 0;
            width: 20px;
            height: 20px;
            border: 1px solid $color_cian;
            border-radius: 5px;
            margin: 0;
            &:after{
                font-size: 20px;
                line-height: 18px;
                width: 20px;
                height: 20px;
                text-align: center;
            }
            @media only screen and (min-width: 992px){
                display: block;
            }
            &:focus{
                outline: inherit;
            }
        }
        .swiper-button-next.swiper-button-disabled, .swiper-button-prev.swiper-button-disabled{
            background: $color_cian;
            color: #ffffff;
        }
        .swiper-button-next, .swiper-container-rtl .swiper-button-prev{
            right: 0;
            margin-right: 30%;
        }
        .swiper-button-prev, .swiper-container-rtl .swiper-button-next{
            left: 0;
            margin-left: 30%;
        }
    }

}