@mixin root-tipography {
    font-family: 'Telefonica';
    font-style: normal;
    font-weight: normal;
}

@mixin root-tipography-regular {
    font-family: 'Telefonica';
    font-style: normal;
    font-weight: 300;
}

@mixin root-tipography-bold {
    font-family: 'Telefonica';
    font-style: normal;
    font-weight: 700;
}

.col--pe-titulo{
    @include root-tipography-regular;
    font-size: 25px;
    line-height: 31px;
    @media only screen and (min-width: 992px) {
        font-size: 35px;
        line-height: 44px;
    }        
}
.col--pe-subtitulo{
    @include root-tipography-regular;
    font-size: 18px;     
    line-height: 22px;
    @media only screen and (min-width: 992px) {
        font-size: 24px;
        line-height: 30px;
    }
}
.col--pe-descripcion{
    @include root-tipography-regular;
    font-size: 16px;     
    line-height: 18px;
    @media only screen and (min-width: 992px) {
        font-size: 18px;     
        line-height: 22px;
    }
}